.login {
    background-color: black;
    color: white;
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
}


.login__logo {
    margin-top: 20px;
    margin-bottom: 20px;
    object-fit: contain;
    width: 100px;
    margin-left: auto;
    margin-right: auto;
}

.login__container {
    width: 300px;
    height: fit-content;
    display: flex;
    flex-direction: column;
    border-radius: 5px;
    border: 1px solid lightgray;
    padding: 20px;
}

.login__container h1 {
    font-weight: 500;
    margin-bottom: 20px;
}

.login__container form h5 {
    margin-bottom: 5px;
}

.login__container form input {
    height: 30px;
    margin-bottom: 10px;
    background-color: white;
    width: 98%;
}

.login__container p{
    margin-top: 15px;
    font-size: 12px;
}

.login__signInButton {
    height: 30px;
    width: 100%;
    background: #f0c14b;
    border-radius: 2px;
    border: 1px solid;
    margin-top: 10px;
    padding: 3px;
    border-color: #aa8873 #9c7e31 #846a29;
    color: #111;
}

.login__registerButton {
    border-radius: 2px;
    width: 100%;
    height: 30px;
    border: 1px solid darkgray;
    margin-top: 10px;
}